/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: none;
    border-radius: 2px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #555555;
    border-radius: 2px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #888888;
}