.arrow-up {
    height: 40px;
    width: 40px;
    display: block;
    border: 1px solid #fff;
    position: relative;
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    overflow: hidden;
}

.arrow-slide {
    left: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    background: #fff;
    position: absolute;
    display: block;
    z-index: 0;
}

.left-arm {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    top: 19px;
    left: 3px;
    width: 20px;
    display: block;
    transform: rotate(-45deg);
}

.left-arm:after {
    content: "";
    background-color: #fff;
    width: 20px;
    height: 1px;
    display: block;
    border-radius: 1px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    transform-origin: right center;
    z-index: -1;
}

.right-arm {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    top: 19px;
    left: 17px;
    width: 20px;
    display: block;
    transform: rotate(45deg);
    border-radius: 2px;
}

.right-arm:after {
    content: "";
    background-color: #fff;
    width: 20px;
    height: 1px;
    display: block;
    border-radius: 1px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    transform-origin: left center;
    z-index: -1;
}

.arrow-up:hover {
    transition: all .1s;
}

.arrow-up:hover .left-arm:after {
    transform: rotate(-10deg);
}

.arrow-up:hover .right-arm:after {
    transform: rotate(10deg);
}

.arrow-up:hover .arrow-slide {
    transition: all .4s ease-in-out;
    transform: translateY(200%);
}

#nextArrow {
    position: fixed;
    top: 50%;
    right: 2%;
    /*transform: translate(-50%, -50%);*/
    /*-webkit-transform: translate(-50%, -50%);*/
}

#prevArrow {
    position: fixed;
    top: 50%;
    left: 2%;
    /*transform: translate(-50%, -50%);*/
    /*-webkit-transform: translate(-50%, -50%);*/
}

