.arrow-up {
    height: 40px;
    width: 40px;
    display: block;
    border: 1px solid #fff;
    position: relative;
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    overflow: hidden;
}

.arrow-slide {
    left: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    background: #fff;
    position: absolute;
    display: block;
    z-index: 0;
}

.left-arm {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    top: 19px;
    left: 3px;
    width: 20px;
    display: block;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.left-arm:after {
    content: "";
    background-color: #fff;
    width: 20px;
    height: 1px;
    display: block;
    border-radius: 1px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    -webkit-transform-origin: right center;
            transform-origin: right center;
    z-index: -1;
}

.right-arm {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    top: 19px;
    left: 17px;
    width: 20px;
    display: block;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 2px;
}

.right-arm:after {
    content: "";
    background-color: #fff;
    width: 20px;
    height: 1px;
    display: block;
    border-radius: 1px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    -webkit-transform-origin: left center;
            transform-origin: left center;
    z-index: -1;
}

.arrow-up:hover {
    transition: all .1s;
}

.arrow-up:hover .left-arm:after {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
}

.arrow-up:hover .right-arm:after {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}

.arrow-up:hover .arrow-slide {
    transition: all .4s ease-in-out;
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
}

