.scroll-up {
    height: 40px;
    width: 40px;
    display: block;
    outline: 1px solid rgba(255, 255, 255, 0);
    position: relative;
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
}
.scroll-up svg line {
    stroke-width: 1;
    stroke: #fff;
    fill: none;
    stroke-dasharray: 20;
    transition: all .4s ease;
}
.scroll-up:hover svg line.top {
    -webkit-transform: translateX(-40px);
    transform: translateX(-40px);
}
.scroll-up:hover svg line.bottom {
    -webkit-transform: translateX(40px);
    transform: translateX(40px);
}
.scroll-up:hover svg line.left {
    -webkit-transform: translateY(40px);
    transform: translateY(40px);
}
.scroll-up:hover svg line.right {
    -webkit-transform: translateY(-40px);
    transform: translateY(-40px);
}

.scroll-up .left-bar {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    top: 19px;
    left: 5px;
    width: 18px;
    display: block;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.scroll-up .left-bar:after {
    content: "";
    background-color: #fff;
    width: 18px;
    height: 1px;
    display: block;
    border-radius: 1px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    -webkit-transform-origin: right center;
            transform-origin: right center;
    z-index: -1;
}
.scroll-up .right-bar {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    top: 19px;
    left: 17px;
    width: 18px;
    display: block;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border-radius: 2px;
}
.scroll-up .right-bar:after {
    content: "";
    background-color: #fff;
    width: 18px;
    height: 1px;
    display: block;
    border-radius: 1px;
    transition: all 0.5s cubic-bezier(0.25, 1.7, 0.35, 1.5);
    -webkit-transform-origin: left center;
            transform-origin: left center;
    z-index: -1;
}
.scroll-up:hover {
    transition: all .1s;
}
.scroll-up:hover .left-bar:after {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
}
.scroll-up:hover .right-bar:after {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: none;
    border-radius: 2px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #555555;
    border-radius: 2px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #888888;
}
