.tiltContainer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    /*width: 1200px;*/
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.carousel-root {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
}

.tiltContainer .box {
    position: relative;
    width: 300px;
    height: 400px;
    /*margin: 40px 50px;*/
    background: transparent;
    border: white 1px solid;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.tiltContainer .nullbox {
    opacity: 0;
    position: relative;
    width: 300px;
    height: 400px;
    /*margin: 40px 50px;*/
    background: transparent;
    border: white 1px solid;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.tiltContainer .nullbox li, .tiltContainer .nullbox #Unflip, .tiltContainer .nullbox #Source {
    font-size: 0;
}

.tiltContainer .box::before {
    content: 'All';
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 4em;
    font-weight: 900;
    color: #fff;
    font-style: italic;
    opacity: 0;
    transition: 0.5s;
}

.tiltContainer .box::after {
    content: 'BOT';
    position: absolute;
    bottom: 20px;
    right: 20px;
    font-size: 4em;
    font-weight: 900;
    color: #fff;
    font-style: italic;
    opacity: 0;
    transition: 0.5s;
}

/*#box2::before {*/
/*  content: "CRUD";*/
/*}*/

/*#box2::after {*/
/*  content: "API";*/
/*}*/

/*#box3::before {*/
/*  content: "IT'S";*/
/*}*/

/*#box3::after {*/
/*  content: "RAININ'";*/
/*}*/

.tiltContainer .box:hover::before, .tiltContainer .box:hover::after {
    opacity: 0.12;
}

.tiltContainer .box .name {
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    color: #fff;
    width: 100%;
    -webkit-transform: translate3d(0,0,75px);
            transform: translate3d(0,0,75px);
    transition: 0.5s;
    opacity: 0;
    z-index: 10;
}

.tiltContainer .box:hover .name {
    top: 20px;
    opacity: 1;
}

.tiltContainer .box .buy {
    cursor: pointer;
    margin: 20px 25px 20px 20px;
    /*margin-right: auto;*/
    /*margin-left: 0;*/
    position: absolute;
    bottom: 0;
    left: 50px;
    /*left: 70px;*/
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: translate3d(-50%, 0, 75px);
            transform: translate3d(-50%, 0, 75px);
    color: #fff;
    background: transparent;
    padding: 10px 25px;
    border-radius: 30px;
    border: white 1px solid;
    text-decoration: none;
    transition: 0.5s;
    opacity: 0;
    z-index: 10;
}

.tiltContainer .box .sell {
    margin: 20px 20px 20px 25px;
    position: absolute;
    bottom: 0;
    right: -60px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: translate3d(-50%, 0, 75px);
            transform: translate3d(-50%, 0, 75px);
    color: #fff;
    background: transparent;
    padding: 10px 25px;
    border-radius: 30px;
    border: white 1px solid;
    text-decoration: none;
    transition: 0.5s;
    opacity: 0;
    z-index: 10;
}

.tiltContainer .box:hover .buy {
    bottom: 30px;
    opacity: 1;
}

.tiltContainer .box:hover .sell {
    bottom: 30px;
    opacity: 1;
}

.tiltContainer .box .list {
    margin-right: 5px;
    align-items: normal;
    font-size: 0.5em;
}

.tiltContainer .box .list li {
    margin: 10px 0;
}

.tiltContainer .box .circle {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    transition: 0.5s;
    background: #fff;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    z-index: 9;
    opacity: 1;
    -webkit-transform: translate3d(-50%, -50%, 50px);
            transform: translate3d(-50%, -50%, 50px);
}

.tiltContainer .box .product {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 200px;
    z-index: 12;
    transition: 0.5s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: translate3d(-50%, -50%, 50px) rotate(0deg) scale(0.9);
            transform: translate3d(-50%, -50%, 50px) rotate(0deg) scale(0.9);
    opacity: 0.5;
}

.tiltContainer .box:hover .product {
    -webkit-transform: translate3d(-50%, -50%, 100px) rotate(-15deg);
            transform: translate3d(-50%, -50%, 100px) rotate(-15deg);
    opacity: 0.8;
}

.tiltContainer .box:nth-child(1) .buy {
    background: transparent;
    border: white 1px solid;
}

.tiltContainer .box:nth-child(1) .sell {
    background: transparent;
    border: white 1px solid;
}

